import type { ValueToTextRule } from '@ali/4ever-cangjie';
import { IImageLocale } from '../../utils/types';
import Image from '../models';

const valueToText: (locale?: IImageLocale) => ValueToTextRule = (locale) => ({
  name: 'image',

  match(node) {
    return Image.isImage(node);
  },

  convert() {
    const title = locale?.imageTitle;
    return title ? `[${title}]` : '';
  },
});

export default valueToText;
